body {
  background-color: #eeeeee;
  font-family: "Lato", sans-serif;
  overflow-x: hidden; }

a {
  color: #2d3c8e; }

.wrap {
  display: block;
  position: relative; }

/* Navigation Overlays */
.main-purple-overlay {
  position: fixed;
  z-index: 6;
  top: 90px;
  left: 0;
  visibility: hidden;
  opacity: 0;
  background-color: #662d91;
  height: calc(100% - 90px);
  width: 100vw;
  transition: visibility 0s linear 0.4s, opacity 0.2s linear 0.2s; }
  .main-purple-overlay.active {
    visibility: visible;
    opacity: 0.6;
    transition: visibility 0s linear 0.2s, opacity 0.2s linear 0.2s; }

.side-purple-overlay {
  position: fixed;
  z-index: 6;
  top: 90px;
  left: 0;
  visibility: hidden;
  opacity: 0;
  background-color: #662d91;
  height: calc(100vh - 90px);
  width: 250px;
  transition: visibility 0s linear 0.4s, opacity 0.2s linear 0.2s; }
  .side-purple-overlay.active {
    visibility: visible;
    opacity: 0.6;
    transition: visibility 0s linear 0.2s, opacity 0.2s linear 0.2s; }

.btn-holder {
  display: inline-block; }
  .btn-holder + .btn-holder {
    margin-left: 5px; }

/* SEO Preview */
.seo-preview {
  font-family: arial, sans-serif; }
  .seo-preview p {
    margin-bottom: 0; }
  .seo-preview .title {
    font-size: 18px;
    color: #1a0dab;
    line-height: 21px; }
  .seo-preview .url {
    display: block;
    word-wrap: break-word;
    color: #006621;
    font-size: 13px;
    line-height: 16px; }
  .seo-preview .description {
    display: block;
    color: #545454;
    line-height: 18px;
    font-size: 13px; }

/* Variants */
.variant-option-img {
  position: relative;
  display: block;
  padding: 10px 10px 30px 10px;
  border: 2px solid #a74c9d;
  text-align: center;
  background-color: transparent;
  width: 100%; }
  .variant-option-img:hover {
    border-color: #662d91; }
    .variant-option-img:hover .change-img {
      background-color: #662d91; }
  .variant-option-img figure {
    height: 100px; }
    .variant-option-img figure .placeholder-empty {
      margin-top: 10px; }
  .variant-option-img img {
    width: 100px;
    vertical-align: middle; }

.change-img {
  position: absolute;
  bottom: 0;
  border: 0px;
  background-color: #a74c9d;
  color: #fff;
  display: block;
  font-size: 14px;
  left: 0;
  width: 100%;
  text-align: center; }

/* Variant List Navigation */
.variant-list {
  margin: 20px 0 0 0;
  padding: 0;
  list-style: none;
  border-top: 1px solid #eee; }
  .variant-list li {
    border-left: 1px solid #eee;
    border-bottom: 1px solid #eee;
    border-right: 1px solid #eee; }
  .variant-list a {
    display: block;
    padding: 10px;
    background-color: #f6f6f6;
    position: relative; }
    .variant-list a:hover {
      background-color: white;
      text-decoration: none; }
    .variant-list a.active {
      background-color: #2d3c8e;
      color: #fff; }
      .variant-list a.active figure {
        border-color: #212c67; }
      .variant-list a.active:after {
        position: absolute;
        right: 0;
        display: block;
        width: 0;
        height: 0;
        top: 35%;
        right: -10px;
        content: '';
        border-style: solid;
        border-width: 10px 0 10px 10px;
        border-color: transparent transparent transparent #2d3c8e; }
  .variant-list .variant-img, .variant-list .variant-options {
    display: inline-block;
    vertical-align: middle; }
    .variant-list .variant-img figure, .variant-list .variant-options figure {
      border: 3px solid rgba(0, 0, 0, 0.1);
      background-color: #fff;
      width: 50px;
      height: 50px; }
      .variant-list .variant-img figure img, .variant-list .variant-options figure img {
        width: 100%;
        vertical-align: middle; }
        .variant-list .variant-img figure img.placeholder, .variant-list .variant-options figure img.placeholder {
          margin-top: 6px; }
  .variant-list .variant-options {
    margin-left: 15px; }
    .variant-list .variant-options ul {
      list-style: none;
      padding: 0;
      margin: 0; }
      .variant-list .variant-options ul li {
        border: none;
        display: inline-block;
        font-size: .875em; }
        .variant-list .variant-options ul li:last-child:after {
          display: none; }
        .variant-list .variant-options ul li:after {
          content: '';
          width: 10px;
          text-align: center;
          display: inline-block; }

/* Media */
.thumbnail-select {
